import { useMemo } from "react";
import "./styles.css";

const videoSource =
  "https://file-examples.com/storage/fe21590a2962d597a9a51ad/2017/04/file_example_MP4_1920_18MG.mp4";

export default function App() {
  const canShare = useMemo(() => navigator.canShare(), []);
  const buttonText = canShare ? "Click me to Share" : "Can't share";
  return (
    <div className="App">
      <h1>Video Share Sample</h1>
      <h2>Click the share button below to share the video file!</h2>
      <video style={{ width: "100vw" }} src={videoSource}></video>
      <button disabled={!canShare}>{buttonText}</button>
    </div>
  );
}
